@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@600;800&display=swap);
*{
  margin: 0;
  padding: 0;
}

html, body{
  overflow-x: hidden;
}

.app{
  display: flex;
  height: 100vh;
}

.section1{

  height: 100vh;
  width: 100vw;


  background-image: url(/static/media/back1.73f24435.png);
  background-size: auto 100%;
  background-position: right;

  display: flex;
  align-items: center;
  justify-content: center;
}

.section2{
  height: 100vh;
  width: 100vw;

  background-color: green;
}

.section3{
  height: 100vh;
  width: 100vw;

  background-color: blue;
}

/* Color Theme Swatches in Hex */
{ color: #F2F2F2; }
{ color: #D9D9D9; }
{ color: #8C8C8C; }
{ color: #595959; }
{ color: #0D0D0D; }

.fpage{
  color: #D9D9D9;

  width: 80%;
  height: 80%;

  display: grid;
  grid-template:
  "a b" 1fr
  "c c" 1.2fr
  /400px 150px;
}

.name-big{
  color: #D9D9D9;
  margin-top: 20vh;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 800;
  font-size: 6rem;
}

.fname{
  grid-area: a;

}

.sname{
  grid-area: b;
  text-align: left;
}

.introtext{
  grid-area: c;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 600;
  color: #A6A6A6;
  font-size: 2.2rem;
  width:75vw;
}

@media (orientation: portrait) {
  .fpage{
    grid-template:
    "a" 1fr
    "b" 2.3fr
    "c" 1.5fr;
  }
  .name-big{
    margin-top: 0;
    font-size: 4.3rem;
    line-height: 0;
  }

  .introtext{
    width: 60vw;
    font-size: 2rem;
  }

}

